import React, { Component } from 'react';
import Carousel from 'react-bootstrap/Carousel';
import forestImg1 from '../assets/5.jpg';
import forestImg2 from '../assets/6.jpg';
import forestImg4 from '../assets/7.jpg';

export default class CarouselBox extends Component {
    render() {
        return (
            <Carousel>
                <Carousel.Item>
                    <img
                        className="d-block w-100"
                        src={forestImg1}
                        alt="Forest"
                    />
                    <Carousel.Caption>
                        <h3>Elephant</h3>
                        <p>Loren ipsum dolor</p>
                    </Carousel.Caption>
                </Carousel.Item>
                <Carousel.Item>
                    <img
                        className="d-block w-100"
                        src={forestImg2}
                        alt="Forest"
                    />
                    <Carousel.Caption>
                        <h3>Forest image</h3>
                        <p>Loren ipsum dolor</p>
                    </Carousel.Caption>
                </Carousel.Item>
                <Carousel.Item>
                    <img
                        className="d-block w-100"
                        src={forestImg4}
                        alt="Forest"
                    />
                    <Carousel.Caption>
                        <h3>Forest image UP</h3>
                        <p>Loren ipsum dolor</p>
                    </Carousel.Caption>
                </Carousel.Item>
            </Carousel>
        )
    }
}
